import {useState, useEffect} from 'react'
import {BrowserRouter as Router, Route, Redirect, useHistory} from 'react-router-dom'
import Header from './components/Header'
import Tasks from './components/Tasks'
import AddTask from './components/AddTask'
import Footer from './components/Footer'
import About from './components/About'
import SignUp from './components/SignUp'
import SignIn from './components/SignIn'
import Front from './components/Front'
import Delete from './components/Delete'
import Sponsor from './components/Sponsor'

var username = ''
var password = ''
var day = ''

function App() {
  const [showAddTask, setShowAddTask] = useState(false)
  const [tasks, setTasks] = useState([])
	const history = useHistory()

  const signup = async (usernam, passwor) => {
    const res = await fetch('https://Task-Manager-Backend.robinheqd.repl.co/signup', {
      method: 'POST',
      headers: {
        'Content-type': 'application/json'
      },
      body: JSON.stringify({usernam, passwor})
    })
    const data = await res.json()
    if (data === "Y") {
	    username = usernam
	    password = passwor
	    day = ''
	    console.log(true)
	    console.log(username, password)
	    setTasks(await fetchTasks())
	    window.localStorage.setItem("username", username)
	    window.localStorage.setItem("password", password)
	    return true
    } else {
	    console.log(false)
	    return false
    }
  }

  const signin = async (usernam, passwor) => {
    const res = await fetch('https://Task-Manager-Backend.robinheqd.repl.co/signin', {
      method: 'POST',
      headers: {
        'Content-type': 'application/json'
      },
      body: JSON.stringify({usernam, passwor})
    })
    const data = await res.json()
    console.log(data[1])
    if (data[0] === "Y") {
	    username = usernam
	    password = passwor
		console.log(username)
	    setTasks(await fetchTasks())
	    console.log(tasks)
	    window.localStorage.setItem("username", username)
	    window.localStorage.setItem("password", password)
	    return true
    } else {
	    console.log(false)
	    return false
    }
  }

  const signout = () => {
	  username = ''
	  password = ''
	  console.log(username, password)
  }

  const fetchTasks = async () => {
	const res = await fetch(`https://Task-Manager-Backend.robinheqd.repl.co/allTasks/${username}`)
	try {
		console.log("h")
    		const data = await res.json()
    		return data
	} catch(err) {
		console.log("n")
	}
  }

  const ondelete = async () => {
	  const res = await fetch(`https://Task-Manager-Backend.robinheqd.repl.co/delete/${username}`)
	  document.cookie = "username='';path='/';"
	  document.cookie = "password='';path='/';"
	  return await res.json()
  }

  const fetchTask = async (id) => {
    const res = await fetch(`https://Task-Manager-Backend.robinheqd.repl.co/tasks/${username}/${id}`)
    const data = await res.json()
    return data
  }

  const addTask = async (task) => {
    const res = await fetch('https://Task-Manager-Backend.robinheqd.repl.co/tasks', {
      method: 'POST',
      headers: {
        'Content-type': 'application/json'
      },
      body: JSON.stringify({task, username})
    })
    const data = await res.json()
    setTasks([...tasks, data])
  }

  const deleteTask = async (id) => {
    const res = await fetch(`https://Task-Manager-Backend.robinheqd.repl.co/tasks/${username}/${id}`, {
      method: 'DELETE',
    })
    setTasks(tasks.filter((task) => task.id !== id))
  }

  const toggleReminder = async (id) => {
    const  taskToToggle = await fetchTask(id)
    const updTask = {...taskToToggle, reminder: !taskToToggle.reminder}
    const res  = await fetch(`https://Task-Manager-Backend.robinheqd.repl.co/tasks/${username}/${id}`, {
      method: 'PUT',
      headers: {
        'Content-type': 'application/json'
      },
      body: JSON.stringify(updTask)
    })
    const data = await res.json()
    setTasks(tasks.map((task) => task.id === id ? {...task, reminder: data.reminder} : task))
  }

	if (username === "" && window.location.pathname == "/tasks"){
		// window.location.replace("https://task-manager-2.robinheqd.repl.co/signin")
	}

  return (
	  <>
	<Router>
	<div className="container">
	<Header onAdd={() => setShowAddTask(!showAddTask)} showAdd={showAddTask} name={username} onSignout={signout} signin={signin} />
	<Route path='/Task-Manager/'exact render={(props) => (
		<>
		<Front username={username} signin={signin} />
		</>
	)} />
	<Route path='/signin'exact render={(props) => (
		<>
		<SignIn onSignin={signin} />
		</>
	)} />
	<Route path='/signup'exact render={(props) => (
		<>
		<SignUp onSignup={signup} usernam={username} />
		</>
	)} />
	<Route path='/tasks'exact render={(props) => (
		<>
		{showAddTask && <AddTask onAdd={addTask} />}
		<p>{tasks.length > 0 ? <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder} username={username} signin={signin} /> : 'No Tasks To Show'}</p>
		<Delete onSignout={signout} onDelete={ondelete} />
		</>
	)} />
	<Route path='/about' component={About} />
	<Footer />
	</div>
    </Router>
    </>
  );
}

export default App;
