import propTypes from 'prop-types'
import Button from './Button'
import {useLocation} from 'react-router-dom'
import {useEffect} from 'react'

const Header = ({title, onAdd, showAdd, name, onSignout, signin}) => {
    const location = useLocation()
	
	useEffect(async () => {
	  if (window.localStorage.getItem("username") && window.location.pathname == "/") {
		  if (await signin(window.localStorage.getItem("username"), window.localStorage.getItem("password"))) {
			  window.history.pushState({}, "", "/tasks")
			  const navEvent = new PopStateEvent('popstate');
    		  window.dispatchEvent(navEvent);
		  }
	  }
  }, [])

    return (
		<>
			<header className='header'>
			<h1>{title}</h1>
			{location.pathname === '/tasks' && <> <Button colour={showAdd ? 'red' : 'green'} text={showAdd ? 'Hide' : 'Show'} onClick={onAdd} /> <Button colour={'black'} text={'Sign Out'} onClick={() => {onSignout(); window.history.pushState({}, "", "/")
			const navEvent = new PopStateEvent('popstate');
			window.dispatchEvent(navEvent);}} /> </>}
			</header>
			<p style={{textAlign: "center"}}><b>{location.pathname === "/tasks" && name}</b></p>
		</>
    )
}

Header.defaultProps = {
    title: 'Task Tracker',
}

Header.propTypes = {
    title: propTypes.string.isRequired,
}

export default Header
